import React from 'react';
import styled from 'styled-components';
import Media from 'react-media';

import * as DISPLAYS from '../constants/displays';
import Vertical from '../components/Vertical';
import Link from '../components/Link';
import Button from '../components/Button';
import ReorderOption from '../icons/ReorderOption';

const Container = styled.nav`
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: #ffffff;
  box-shadow: 0px 1px 20px -10px rgba(0, 0, 0, 0.75);
  color: #a8a8a8;
`;

const NavigationDefault = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
`;

const NavigationMobile = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
`;

const NavigationLink = styled(Link)`
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.08em;
`;

const NavigationLinkDefault = styled(NavigationLink)`
  margin: 0 20px;
`;

const NavigationLinkMobile = styled(NavigationLink)`
  margin: 5px 0;
`;

const Navigation = ({ places, isOpen, toggleOpen }) => (
  <header>
    <Container>
      <Media query={DISPLAYS.MOBILE}>
        {matches =>
          matches ? (
            <NavigationMobile>
              {isOpen && (
                <Vertical>
                  {places.map(place => (
                    <NavigationLinkMobile
                      key={place.to}
                      to={place.to}
                    >
                      {place.label}
                    </NavigationLinkMobile>
                  ))}
                </Vertical>
              )}
              <div>
                <Button
                  aria-label="toggle navigation"
                  onClick={toggleOpen}
                >
                  <ReorderOption height="24px" width="24px" />
                </Button>
              </div>
            </NavigationMobile>
          ) : (
            <NavigationDefault>
              {places.map(place => (
                <NavigationLinkDefault key={place.to} to={place.to}>
                  {place.label}
                </NavigationLinkDefault>
              ))}
            </NavigationDefault>
          )
        }
      </Media>
    </Container>
  </header>
);

export default Navigation;
