import React from 'react';

const withNoWhitespace = Component =>
  class NoWithespace extends React.Component {
    constructor(props) {
      super(props);
      this.ref = React.createRef();
      this.state = {
        viewBox: null,
      };
    }
    componentDidMount() {
      if (this.ref.current) {
        const box = this.ref.current.getBBox();
        this.setState({
          viewBox: [box.x, box.y, box.width, box.height].join(' '),
        });
      }
    }
    render() {
      return (
        <Component
          {...this.props}
          ref={this.ref}
          viewBox={this.state.viewBox}
        />
      );
    }
  };

