import React from 'react';
import styled from 'styled-components';
import { graphql } from 'gatsby';

import * as DISPLAYS from '../constants/displays';
import Layout from '../components/Layout';
import Horizontal from '../components/Horizontal';
import Link from '../components/Link';
import Button from '../components/Button';
import Image from '../components/Image';

// used in GraphQL query
const IMAGE_MAX_WIDTH = 720;
const IMAGE_MIN_WIDTH = 320;

const Container = styled.div`
  max-width: 1024px;
  display: flex;
  flex-direction: column;
`;

const BlogItem = Horizontal.extend`
  margin: 40px 0;

  @media ${DISPLAYS.TABLET} {
    flex-direction: column;

    &:not(:first-child) {
      margin: 20px 0;
    }

    &:first-child {
      margin-top: 0;
      margin-bottom: 20px;
    }
  }
`;

const BlogItemImage = styled.div`
  width: 100%;
  min-width: ${IMAGE_MIN_WIDTH}px;
  max-width: ${IMAGE_MAX_WIDTH}px;
`;

const BlogItemText = styled.div`
  margin: 0 20px;

  @media ${DISPLAYS.TABLET} {
    margin-top: 20px;
  }
`;

export default function Blog({ data: { site, allMdx } }) {
  return (
    <Layout site={site}>
      <Container>
        {allMdx.edges.map(({ node: post }) => (
          <BlogItem key={post.id}>
            {post.frontmatter.banner && (
              <BlogItemImage>
                <Image
                  sizes={
                    post.frontmatter.banner.childImageSharp.sizes
                  }
                />
              </BlogItemImage>
            )}

            <BlogItemText>
              <h2>
                <Link to={post.frontmatter.slug}>
                  {post.frontmatter.title}
                </Link>
              </h2>

              <small>{post.frontmatter.date}</small>

              <p>{post.excerpt}</p>

              <Button
                aria-label="read more"
                to={post.frontmatter.slug}
              >
                Read more
              </Button>
            </BlogItemText>
          </BlogItem>
        ))}
      </Container>
    </Layout>
  );
}

export const pageQuery = graphql`
  query {
    site {
      ...site
    }
    allMdx(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 300)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            banner {
              childImageSharp {
                sizes(maxWidth: 720) {
                  ...GatsbyImageSharpSizes
                }
              }
            }
            slug
            categories
            keywords
          }
        }
      }
    }
  }
`;
