import React, { Component, Fragment } from 'react';
import { graphql } from 'gatsby';
import Helmet from 'react-helmet';
import styled, { injectGlobal } from 'styled-components';
import { MDXProvider } from '@mdx-js/tag';

import 'prismjs/themes/prism-okaidia.css';

import * as COLORS from '../constants/colors';
import mdxComponents from './mdx';
import Navigation from './Navigation';

const PLACES = [
  { to: '/', label: 'Home' },
  { to: '/blog', label: 'Blog' },
  { to: '/categories', label: 'Categories' },
];

injectGlobal`
  html, body {
    margin: 0;
    padding: 0;

    font-family: "Roboto", Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 1.58;
    letter-spacing: -.003em;

    color: #333333;
  }

  h1 {
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  h2 {
    text-transform: uppercase;
    margin: 0 0 10px;
    font-size: 24px;
    letter-spacing: 0.08em;
    font-weight: 400;
    line-height: 1.1;
    color: #333333;
  }

  small {
    font-size: 12px;
    color: #717171;
  }

  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  a {
    color: #333333;
    text-decoration: none;
  }

  a:visited {
    color: #333333;
  }

  a:hover {
    color: ${COLORS.main};

    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
  }

  ${() => {
    /* PrismJS */ return null;
  }}

  pre {
    background-color: #2f1e2e !important;
    border-radius: 4px;
    font-size: 14px;
    border-left: 5px solid ${COLORS.main};
  }

  .gatsby-highlight-code-line {
    background-color: #4f424c;
    display: block;
    margin-right: -1em;
    margin-left: -1em;
    padding-right: 1em;
    padding-left: 1em;
  }

  ${() => {
    /* Gatsby Image */ return null;
  }}

  .gatsby-resp-image-wrapper {
    max-width: 100% !important;
  }
`;

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const Content = styled.div`
  margin-top: ${props =>
    props.isOpen ? props.places.length * 45 : 55}px;

  display: block;
  align-self: center;
`;

export default class Layout extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: false,
    };
  }

  toggleOpen = () => {
    this.setState(state => ({ isOpen: !state.isOpen }));
  };

  render() {
    const { site, frontmatter = {}, children } = this.props;

    const { isOpen } = this.state;

    const {
      title,
      description: siteDescription,
      keywords: siteKeywords,
    } = site.siteMetadata;

    const {
      keywords: frontmatterKeywords,
      description: frontmatterDescription,
    } = frontmatter;

    const keywords = (frontmatterKeywords || siteKeywords).join(', ');
    const description = frontmatterDescription || siteDescription;

    return (
      <Fragment>
        <Helmet
          title={title}
          meta={[
            { name: 'description', content: description },
            { name: 'keywords', content: keywords },
          ]}
        >
          <html lang="en" />

          <noscript>
            You need to enable JavaScript to see this website!
          </noscript>
        </Helmet>

        <MDXProvider components={mdxComponents}>
          <Container>
            <Navigation
              places={PLACES}
              isOpen={isOpen}
              toggleOpen={this.toggleOpen}
            />
            <Content isOpen={isOpen} places={PLACES}>
              {children}
            </Content>
          </Container>
        </MDXProvider>
      </Fragment>
    );
  }
}

export const pageQuery = graphql`
  fragment site on Site {
    siteMetadata {
      title
      description
      author
      keywords
    }
  }
`;
